<template>
    <div>
        <detail-header>
            <my-nav :scroll="true"></my-nav>
        </detail-header>
        <div class="info-img">
            <el-image src="https://s1.tuchong.com/content-image/202104/fe7dcd931b140180ce3b612df4b131b8.jpg"></el-image>
            <div class="detail-info">
                <h1 class="page-topic__info-title">{{ category[`${secondCategory}`]}}</h1>
                <p class="page-topic__info-total">共77个</p>
                <span class="page-topic__info-keywords">立夏</span><span>夏至</span>

            </div>
        </div>
        <div class="detail-all-img">
            <el-col :span="5" v-for="item in showData" :key="item.id" :offset="1">
                <div class="thumbnail" @click="changeDialog(item.id)">
                    <el-image :src="item.url" fit="cover">
                        <div slot="placeholder" class="image-slot">
                            加载中<span class="dot">...</span>
                        </div>
                    </el-image>
                    <span class="search-result__opt-item" v-if="!item.collection">收藏</span>
                    <span class="search-result__opt-item" v-if="item.collection" style="width: 50px">取消收藏</span>
                    <i :class="{'el-icon-star-off': !item.collection, 'el-icon-star-on': item.collection}" @click.stop="collectionWork(item.id, item.collection)"></i>
                </div>
            </el-col>
        </div>
        <detail-info :dialog-table-visible.sync="dialogTableVisible"
                     v-if="dialogTableVisible"
                     :detail-data.sync="detailInfo"
                     :detail-list.sync="detailList"
                     :collection="1"
        ></detail-info>
    </div>
</template>

<script>
    import DetailHeader from "../components/DetailHeader";
    import DetailInfo from "./detail-info"
    import { PAGINATION } from '@/helpers/baseData'
    import { getWorkDetail, workCollection, workCollectionCancel } from '@/service/info-api'
    import MyNav from "../components/MyNav";
    import { megSuc } from "../../utils/message";

    export default {
        name: "Detail",
        created() {
            getWorkDetail(this.secondCategory, this.userId)
            .then((res) => {
                if (res.code === 2000){
                    this.detailList = res.detail_list
                }
            })

        },
        data(){
          return {
              dialogTableVisible: false,
              category: PAGINATION.category,
              secondCategory: this.$route.params.id,
              userId: this.$store.getters.consumerInfo.id || 0,
              detailList: [],
              detailInfo: {}
          }
        },
        components: {
            DetailHeader,
            DetailInfo,
            MyNav
        },
        computed: {
            showData: {
                get: function() {
                    return this.detailList
                },
                set: function (newValue) {
                    this.detailList = newValue
                }
            }
        },
        methods: {
            changeDialog(id){
                this.dialogTableVisible = true
                this.detailInfo = this.detailList.find(item => item.id === id)
            },
            collectionWork(work_id, is_collection){
                let request_data = {
                    user_id: this.userId,
                    work_id: work_id
                }
                console.log(is_collection)
                if (!is_collection){
                    workCollection(request_data)
                        .then((res) => {
                            if (res.code === 2000){
                                megSuc('收藏成功')
                                getWorkDetail(this.secondCategory, this.userId)
                                    .then((res) => {
                                        if (res.code === 2000){
                                            this.detailList = res.detail_list
                                        }
                                    })
                            }
                        })
                } else {
                    workCollectionCancel(request_data)
                    .then((res) => {
                        if (res.code === 2000){
                            megSuc('取消收藏成功')
                            getWorkDetail(this.secondCategory, this.userId)
                                .then((res) => {
                                    if (res.code === 2000){
                                        this.detailList = res.detail_list
                                        console.log(this.showData[0], this.detailList[0])
                                    }
                                })
                        }
                    })
                }

            }
        }
    }
</script>

<style lang="scss">
    .info-img{
        position: absolute;
        top: 72px;
        height: 500px;
    }
    .detail-info{
        position: absolute;
        top: 60%;
        left: 50px;
    }
    .page-topic__info-title{
        margin: 0;
        padding: 0;
        font-size: 32px;
        line-height: 45px;
        font-weight: 500;
        color: #ffffff;
    }
    .page-topic__info-total{
        margin: 4px 0 0;
        font-size: 14px;
        line-height: 20px;
        color: #fff;
    }
    .page-topic__info-keywords{
        color: #fff;
        font-size: 14px;
        border: 1px solid hsla(0,0%,100%,.8);
        border-radius: 15.5px;
        padding: 2px 10px;
        margin-right: 10px;
        margin-top: 12px;
        cursor: pointer;
    }
    .detail-all-img{
        margin-top: 600px;
        .el-col{
            height: 237px;
            line-height: 237px;
        };
        .thumbnail{
            height: 217px;
            width: 290px;
            background-color: black;
            padding: 0 10px;
            max-width: 290px;
            border-radius: 10px;
            position: absolute;
            .el-image{
                height: 217px;
                width: 290px;
            }
            .search-result__opt-item{
                position: absolute;
                height: 15px;
                width: 25px;
                line-height: 15px;
                font-size: 5px;
                margin-left: 4px;
                padding: 4px 6px;
                border-radius: 5px;
                top: 160px;
                right: 20px;
                background-color: rgba(0,0,0,.6);
                opacity: 0;
                transition: opacity .2s;
            }
            .el-icon-star-off, .el-icon-star-on{
                position: absolute;
                top: 190px;
                right: 30px;
                border: 1px solid black ;
                border-radius: 5px;
                height: 20px;
                width: 20px;
                line-height: 20px;
                opacity: 0;
                transition: opacity .2s;

            }

            &:hover{
                .el-icon-star-off, .el-icon-star-on{
                    opacity: 1;
                }
                .search-result__opt-item{
                    background-color: black;
                    color: #ffffff;
                    opacity: 1;
                }
            }
        }
    }




</style>
